export default {
	data() {
		return {
			filNum: '',
			address: '',
			n: true
		};
	},
	onLoad(option) {
		this.filNum = option.fil_num;
		this.address = option.wallet_value;
	},
	methods: {
		save() {
			uni.navigateBack({
				delta: 2
			});
		},
		next: function() {
			var that = this;
			that.n = false;
		},
		back: function() {
			var that = this;
			that.n = true;
		}
	},
	onBackPress(options) {
		if (options.from === 'backbutton') {
			uni.navigateBack({
				delta: 2
			});
		}
	}
};
